@import "../../styles/mixins";

h4 {
  @include secondary-title;
}
.Container {
  text-align: center;
}
.Form {
  width: 100%;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  input {
    width: 100%;
    padding: 5px 0px;
    margin: 5px 0px;
  }
  select {
    width: 100%;
    padding: 5px 0px;
    margin: 5px 0px;
  }
  textarea {
    width: 100%;
    height: 15vh;
    padding: 5px 0px;
    margin: 5px 0px;
    resize: none;
  }
}
